<template>
    <div class="QRCode">
        <h1>{{msg}}</h1>
        <a :href="msg">wxxz</a>
        <a href="http://baidu.com">baidu</a>
        
    </div>
</template>

<script>
export default {
    name: "QRCode",
    data () {
        return {
           msg:this.appSource()
        }
    },
    mounted () {
        
        // window.location = this.appSource();
    },
    methods: {
        appSource () {
            let ua = navigator.userAgent;
            let isWX = !!/MicroMessenger/i.test(ua);
            let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            let isiPhone = ua.indexOf('iPhone') > -1 || ua.indexOf('Mac') > -1;
            var downloadUrl = "http://api.honpe.com:81/honpeapp/honpe.apk?mp.wexin.qq.com";
            if (isiOS || isiPhone) {
                downloadUrl = "https://itunes.apple.com/app/id1093743730";
            } 
            if (isWX) {
            //    downloadUrl = "微信浏览器";
            }
            return downloadUrl;
        }
    }
}
</script>



